<template>
  <router-view/>
</template>
<script>
import { mapMutations } from 'vuex'
import { getToken, setToken } from 'utils/tool'

export default {
  name: 'Index',
  mounted () {
    // token
    let newToken = this.$route.query.token
    if (newToken) {
      setToken(newToken)
      this.setToken(newToken)
      this.$router.replace({name: 'Home'})
    } else {
      let nowToken = getToken()
      if (nowToken) {
        this.setToken(nowToken)
      } else {
        // query上没有，cookie上也没有，跳转到错误提示页
        this.$router.replace({name: 'Error'})
      }
    }
  },
  methods: {
    ...mapMutations({
      setToken: 'SET_TOKEN'
    })
  }
}
</script>



